import axios from 'axios';
import React, { useEffect, useState } from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { categories } from '../../interface/Categories';
export default function CategoriesSilder() {
    let BaseUrl = `https://route-ecommerce.onrender.com`
    const [categories, setcategories] = useState<categories[]>([])
    async function getDataCategories() {
        let { data } = await axios.get(`${BaseUrl}/api/v1/categories`)
        console.log(data.data);
        setcategories(data.data)
    }
    useEffect(() => {
        getDataCategories()
    }, [])


    return <>
        <h4 >Shop popular Categories</h4>
        <OwlCarousel autoPlay={true} autoplayTimeout={5000} smartSpeed={100} autoplaySpeed={true} loop items={5} >
            {categories.map((Categories) => {
                return <div className='cursor-pointer' id={Categories._id} key={Categories._id}>
                    <img src={Categories.image} className='w-100' height={200} alt="" />
                    <p className='font-sm fw-bold text-center'>{Categories.name}</p>
                </div>
            })}
        </OwlCarousel>
    </>

}

