import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import detials from '../../interface/prodectdeltils';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Helmet } from "react-helmet";
import { cartContext } from '../../Context/carts';
import { toast } from 'react-hot-toast';
export default function ProdectDetials() {
    const [Loading, setLoading] = useState(false)
    let { addprodectcard, setnumOfCartItems } = useContext(cartContext)
    async function addprodect(productId: any) {
        let response = await addprodectcard(productId)
        console.log(response);
        if (response.data.status === 'success') {
            setnumOfCartItems(response.data.numOfCartItems)
            toast.success(response.data.message);
        } else {
            toast.error(response.data.message)

        }

    }
    let { id } = useParams()
    let BaseUrl = `https://route-ecommerce.onrender.com`
    let [listprodectDetials, setprodectDetials]: any = useState<detials[]>([])

    useEffect(() => {
        getProdectDetials()
    }, [])
    async function getProdectDetials() {
        setLoading(true)
        let { data } = await axios.get(`${BaseUrl}/api/v1/products/${id}`)
        setprodectDetials(data.data)
        console.log(data.data);
        setLoading(false)
    }


    return <>
        <Helmet>
            <title>ProdctsDetials</title>
        </Helmet>
        <div className='row pt-4 align-items-center'>
            {Loading ? <div className='loading d-flex align-items-start justify-content-center bg-white position-fixed start-0 bottom-0 '>
                <div className="sk-cube-grid">
                    <div className="sk-cube sk-cube1"></div>
                    <div className="sk-cube sk-cube2"></div>
                    <div className="sk-cube sk-cube3"></div>
                    <div className="sk-cube sk-cube4"></div>
                    <div className="sk-cube sk-cube5"></div>
                    <div className="sk-cube sk-cube6"></div>
                    <div className="sk-cube sk-cube7"></div>
                    <div className="sk-cube sk-cube8"></div>
                    <div className="sk-cube sk-cube9"></div>
                </div>

            </div> :
                <>
                    <div className='col-lg-4 col-md-6' >
                        <OwlCarousel className='owl-theme' items={1} loop>
                            {listprodectDetials.images?.map((element: any) => {
                                return <div className='iamges' key={element}  >
                                    <img src={element} className='w-100' alt="" />
                                </div>

                            })

                            }          </OwlCarousel>
                    </div>
                    <div className='col-lg-7 col-md-6'>
                        <div className='text'>
                            <h3 className='text-main fw-bold'>{listprodectDetials?.title}</h3>
                            <p className='mt-2 mb-1 text-black fw-bolder'>{listprodectDetials?.subcategory?.map((e: any) => e.name)}</p>
                            <p className=' text-black font-lg h pt-1'>{listprodectDetials?.description}</p>
                        </div>
                        <div className='d-flex justify-content-between pb-2'>
                            <span>{listprodectDetials?.price} EGP</span>
                            <div className='icon'>
                                <i className="fa-solid fa-star rating-color"></i><span className='text-black'>{listprodectDetials?.ratingsAverage}</span>  </div>
                        </div>
                        <button onClick={() => addprodect(listprodectDetials._id)} className='btn bg-main text-white w-100'>Add card</button>
                    </div>
                </>
            }
        </div>


    </>
}
