import React, { useContext } from 'react'
import logo from '../../assets/images/freshcart-logo.svg'
import { Link, NavLink } from "react-router-dom";
import { cartContext } from '../../Context/carts';
export default function Navbar({ userdata, logout }) {
  let { numOfCartItems } = useContext(cartContext)

  return <>

    <nav className="navbar navbar-expand-lg fixed-top navbar-light bg-light">
      <div className="container">
        <Link className="navbar-brand" to="/">
          <img src={logo} alt="" />
        </Link>
        <button className="navbar-toggler d-lg-none" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavId" aria-controls="collapsibleNavId"
          aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="collapsibleNavId">
          {userdata !== null ? <ul className="navbar-nav me-auto mt-2 mt-lg-0">
            <li className="nav-item ">
              <NavLink className={({ isActive }) => (isActive ? 'active nav-link' : 'inactive nav-link')} to="/">Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="carts">Cart</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="prodcts">Prodcts</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="categories">Categories</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="brands">Brands</NavLink>
            </li>

          </ul> : ''}

          <ul className="navbar-nav ms-auto mt-2 mt-lg-0">
            <li className="nav-item d-flex align-items-center">
              <i className='fa-brands mx-2 fa-instagram cursor-pointer'></i>
              <i className="fa-brands mx-2 fa-facebook cursor-pointer"></i>
              <i className='fa-brands mx-2 fa-twitter cursor-pointer'></i>
              <i className='fa-brands mx-2 fa-tiktok cursor-pointer'></i>
              <i className='fa-brands mx-2 fa-linkedin cursor-pointer'></i>
              <i className='fa-brands mx-2 fa-youtube cursor-pointer'></i>
              <Link className='position-relative' to={'/carts'}>
                <i className="fa-solid fa-cart-shopping">
                  <span className="position-absolute top-0  start-100 translate-middle badge rounded-pill bg-main">{numOfCartItems}</span></i>
              </Link>
            </li>

            {userdata == null ? <>
              <li className="nav-item">
                <Link className="nav-link" to="login">login</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="register">Register</Link>
              </li>
            </> : <>

              <li className="nav-item">
                <span className="cursor-pointer nav-link" onClick={logout}>Logout</span>
              </li>

            </>
            }
          </ul>
        </div>
      </div>
    </nav>


  </>
}
