import React, { useEffect, useState } from 'react'
import { categories } from '../../interface/Categories';
import axios from 'axios';
import { Helmet } from "react-helmet";

export default function Categories() {
  const [Loading, setLoading] = useState(false)
  let BaseUrl = `https://route-ecommerce.onrender.com`
  const [categories, setcategories] = useState<categories[]>([])
  async function getDataCategories() {
    setLoading(true)
    let { data } = await axios.get(`${BaseUrl}/api/v1/categories`)
    console.log(data.data);
    setcategories(data.data)
    setLoading(false)
  }
  useEffect(() => {
    getDataCategories()
  }, [])
  return <>
   <Helmet>
      <title>Categories</title>
    </Helmet>
   {Loading ?
    <div className='loading d-flex align-items-start justify-content-start bg-white position-fixed end-0  start-0 bottom-0 '>
      <div className="sk-cube-grid">
        <div className="sk-cube sk-cube1"></div>
        <div className="sk-cube sk-cube2"></div>
        <div className="sk-cube sk-cube3"></div>
        <div className="sk-cube sk-cube4"></div>
        <div className="sk-cube sk-cube5"></div>
        <div className="sk-cube sk-cube6"></div>
        <div className="sk-cube sk-cube7"></div>
        <div className="sk-cube sk-cube8"></div>
        <div className="sk-cube sk-cube9"></div>
      </div>

    </div>
    : <>

      <div className='row pt-5'>
        {categories.map((Categories) => {
          return <div className='col-lg-3 col-md-6' key={Categories._id}>
            <div className='cursor-pointer' id={Categories._id} >
              <img src={Categories.image} className='w-100' height={200} alt="" />
              <p className='font-lg fw-bold text-center'>{Categories.name}</p>
            </div>
          </div>

        })}
      </div>
    </>
}
</>

  }
