import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import toast, { Toaster } from 'react-hot-toast';
import './App.css';
import Layout from './Components/Layout/Layout';
import { createBrowserRouter, createHashRouter, RouterProvider, useNavigate } from 'react-router-dom'
import Home from './Components/Home/Home';
import Login from './Components/Login/Login';
import Carts from './Components/Carts/Carts';
import Prodcts from './Components/Prodcts/Prodcts';
import ProdectDetials from './Components/ProdectDetials/ProdectDetials';
import Categories from './Components/Categories/Categories';
import Register from './Components/Register/Register';
import jwtDecode from 'jwt-decode';
import ProdectRouter from './Components/ProdectRouter/ProdectRouter';
import Brand from './Components/Brand/Brand';
import Forgetpassword from './Components/Forgetpassword/Forgetpassword';
import ResetPassword from './Components/ResetPassword/ResetPassword';
import { CartContextprovieder } from './Context/carts';
import Checkout from './Components/Checkout/Checkout';

function App() {

  useEffect(() => {
    if (localStorage.getItem('useToken') !== null) {
      SaveUserData();
    }

  }, [])
  const [userdata, setuserdata] = useState(null)

  function SaveUserData() {
    let encodedToken = localStorage.getItem('useToken')
    let DecodeToken = jwtDecode(encodedToken);
    setuserdata(DecodeToken)
  }

  let routers = createHashRouter([
    {
      path: '', element: <Layout setuserdata={setuserdata} userdata={userdata} />, children: [
        { index: true, element: <ProdectRouter><Home /></ProdectRouter> },
        { path: 'carts', element: <ProdectRouter><Carts /></ProdectRouter> },
        { path: 'prodcts', element: <ProdectRouter><Prodcts /></ProdectRouter> },
        { path: 'ProdectDetials/:id', element: <ProdectRouter><ProdectDetials /></ProdectRouter> },
        { path: 'categories', element: <ProdectRouter><Categories /></ProdectRouter> },
        { path: 'brands', element: <ProdectRouter> <Brand /></ProdectRouter> },
        { path: 'checkout', element: <ProdectRouter> <Checkout /></ProdectRouter> },
        { path: 'forgetpassword', element: <Forgetpassword /> },
        { path: 'resetPassword', element: <ResetPassword /> },
        { path: 'login', element: <Login SaveUserData={SaveUserData} /> },
        { path: 'register', element: < Register /> },
      ]
    }


  ])

  return <CartContextprovieder>
    <Toaster />
    <RouterProvider router={routers}></RouterProvider>
  </CartContextprovieder>
}

export default App;
