import { useFormik } from 'formik'
import React, { useState } from 'react'
import intarface from './LoginInterface';
import * as Yup from 'yup'
import { Helmet } from "react-helmet";
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
export default function Login({ SaveUserData }: any) {
  let BaseUrl = `https://route-ecommerce.onrender.com`

  let Navigate = useNavigate()
  const [MessErrors, setMessErrors] = useState()
  const [BtnLoading, setBtnLoading] = useState(false)
  let validate = Yup.object({
    email: Yup.string().required('email is required').email('Invalid email address'),
    password: Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/, 'password must start with uppercase ....'),
  })

  let formik = useFormik({
    initialValues: {
      email: '',
      password: ''
    },
    validationSchema: validate,
    onSubmit: postdata,
  })

  async function postdata(value: any) {
    setBtnLoading(true)
    let { data }: any = await axios.post(`${BaseUrl}/api/v1/auth/signin`, value).catch((error) => {
      console.log(error.response.data.message);
      setMessErrors(error.response.data.message)
      setBtnLoading(false)
    })
    if (data.message == 'success') {
      localStorage.setItem('useToken', data.token)
      SaveUserData();
      setBtnLoading(false)
      Navigate('/')
    }


  }

  return <>
  <Helmet>
      <title>Login</title>
    </Helmet>
    <div className='w-75 mx-auto py-4'>
      <h3>Login</h3>
      {MessErrors ? <div className='alert alert-danger'>{MessErrors}</div> : ''}
      <form onSubmit={formik.handleSubmit}>
        <div className='my-2'>
          <label htmlFor="email">Email:</label>
          <input value={formik.values.email} onBlur={formik.handleBlur} onChange={formik.handleChange} type="email" name="email" id="email" className='form-control mt-1' />
          {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>{formik.errors.email}</div> : ''}
        </div>
        <div className='my-2'>
          <label htmlFor="password">Password:</label>
          <input type="password" value={formik.values.password} onBlur={formik.handleBlur} onChange={formik.handleChange} name="password" id="password" className='form-control mt-1' />
          {formik.errors.password && formik.touched.password ? <div className='alert alert-danger mt-2'>{formik.errors.password}</div> : ''}
          <div className='pt-2 font-lg'>
         <Link to={'/forgetpassword'}><span className='text-main text-center text-decoration-underline'>Forget your password?</span> </Link>

         </div>
        </div>

        {BtnLoading ? <button type='button' className='btn bg-main text-white'><i className='fas fa-spinner fa-spin'></i></button>
          : <button type='submit' disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white'>Login</button>
        }
       
      </form>
    </div>

  </>

}