import axios from "axios";
import { createContext, useEffect, useState } from "react";
export let cartContext = createContext();

export function CartContextprovieder(props) {
    let BaseUrl = `https://route-ecommerce.onrender.com`
    let addheaders = {
        token: localStorage.getItem('useToken')
    }
    const [CartId, setCartId] = useState()
    const [numOfCartItems, setnumOfCartItems] = useState()
    async function getdata() {
        let response = await getLoggedUserCart()
        if (response?.data?.status === 'success') {
            setCartId(response.data.data._id)
            setnumOfCartItems(response.data.numOfCartItems)
        }

    }

    useEffect(() => {
        getdata()
    })
    function addprodectcard(productId) {
        return axios.post(`${BaseUrl}/api/v1/cart`, {
            productId: productId
        }, {
            headers: addheaders
        })
            .then((response) => response)
            .catch((erorr) => erorr)

    }

    function getLoggedUserCart() {
        return axios.get(`${BaseUrl}/api/v1/cart`, {
            headers: addheaders
        })
            .then((response) => response)
            .catch((erorr) => erorr)

    }

    function RemoveData(productId) {
        return axios.delete(`${BaseUrl}/api/v1/cart/${productId}`, {
            headers: addheaders
        })
            .then((response) => response)
            .catch((erorr) => erorr)

    }
    function UpdateData(productId, count) {
        return axios.put(`${BaseUrl}/api/v1/cart/${productId}`, {
            count: count
        }, {
            headers: addheaders
        })
            .then((response) => response)
            .catch((erorr) => erorr)

    }
    function Checkpayment(cartId, shippingAddress) {
        return axios.post(`${BaseUrl}/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`, {
            shippingAddress: shippingAddress
        }, {
            headers: addheaders
        })
            .then((response) => response)
            .catch((erorr) => erorr)

    }
    return <cartContext.Provider value={{ addprodectcard, getLoggedUserCart, RemoveData, UpdateData, Checkpayment ,CartId ,numOfCartItems,setnumOfCartItems}}>
        {props.children}
    </cartContext.Provider>
}