import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { prodect } from '../../interface/prodectHome'
import { Link } from 'react-router-dom'
import { Helmet } from "react-helmet";
import { cartContext } from '../../Context/carts';
import { toast } from 'react-hot-toast';
export default function Prodcts() {
  const [Loading, setLoading] = useState(false)
  let BaseUrl = `https://route-ecommerce.onrender.com`

  const [ProdectList, setProdectList] = useState<prodect[]>([])
  async function getDataProdect() {
    setLoading(true)
    let { data } = await axios.get(`${BaseUrl}/api/v1/products`)
    console.log(data.data);
    setProdectList(data.data)
    setLoading(false)
  }
  useEffect(() => {
    getDataProdect()
  }, [])

  let { addprodectcard ,setnumOfCartItems} = useContext(cartContext)
  async function addprodect(productId: any) {
      let response = await addprodectcard(productId)
      console.log(response);
      if (response.data.status === 'success') {
        setnumOfCartItems(response.data.numOfCartItems)
        toast.success(response.data.message);
      } else {
        toast.error(response.data.message)
  
      }
  
    }
  return <>
    <Helmet>
      <title>Prodcts</title>
    </Helmet>
    {Loading ?
      <div className='loading d-flex align-items-start justify-content-start bg-white position-fixed end-0  start-0 bottom-0 '>
        <div className="sk-cube-grid">
          <div className="sk-cube sk-cube1"></div>
          <div className="sk-cube sk-cube2"></div>
          <div className="sk-cube sk-cube3"></div>
          <div className="sk-cube sk-cube4"></div>
          <div className="sk-cube sk-cube5"></div>
          <div className="sk-cube sk-cube6"></div>
          <div className="sk-cube sk-cube7"></div>
          <div className="sk-cube sk-cube8"></div>
          <div className="sk-cube sk-cube9"></div>
        </div>
      </div> : <div className='row pt-3 pb-4'>
        {ProdectList.map((prodect) => {
          return <div key={prodect._id} className='col-lg-2 col-md-4 h-50'>
            <div id={prodect._id} className='product  cursor-pointer px-2 py-3'>

              <Link key={prodect._id} to={`/ProdectDetials/${prodect._id}`} >
                <div className='images pt-2'>
                  <img src={prodect.imageCover} className='w-100' alt="" />
                </div>
                <div className='text pt-2'>
                  <span className=' text-main fw-bold  font-sm'>{prodect.subcategory.map((e)=>e.name)}</span>
                  <h3 className='h6 text-black fw-bold pt-1'>{prodect.title.split(' ').slice(0, 2).join(' ')}</h3>
                </div>
                <div className='d-flex justify-content-between pb-2'>
                  <span>{prodect.price} EGP</span>
                  <div className='icon'>
                    <i className="fa-solid fa-star rating-color"></i><span className='text-black'>{prodect.ratingsAverage}</span>  </div>
                </div>
              </Link>
                <button onClick={() => addprodect(prodect._id)} className='btn bg-main text-white w-100'>Add card</button>
            </div>
          </div>
        })}


      </div>
    }
  </>

}
