import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { brands } from '../../interface/Brands';
import { Helmet } from "react-helmet";

export default function Brand() {
  let BaseUrl = `https://route-ecommerce.onrender.com`
  const [Loading, setLoading] = useState(false)
  const [isbrands, setisbrands] = useState<brands[]>([])
  async function getDatabrands() {
    setLoading(true)
    let { data } = await axios.get(`${BaseUrl}/api/v1/brands`)
    console.log(data.data);
    setisbrands(data.data)
    setLoading(false)

  }
  useEffect(() => {
    getDatabrands()
  }, [])
  return <>
   <Helmet>
      <title>Brand</title>
    </Helmet>
  {Loading?
    <div className='loading d-flex align-items-start justify-content-start bg-white position-fixed end-0  start-0 bottom-0 '>
      <div className="sk-cube-grid">
        <div className="sk-cube sk-cube1"></div>
        <div className="sk-cube sk-cube2"></div>
        <div className="sk-cube sk-cube3"></div>
        <div className="sk-cube sk-cube4"></div>
        <div className="sk-cube sk-cube5"></div>
        <div className="sk-cube sk-cube6"></div>
        <div className="sk-cube sk-cube7"></div>
        <div className="sk-cube sk-cube8"></div>
        <div className="sk-cube sk-cube9"></div>
      </div>

    </div>
   :<>
    <div className='row pt-5 g-3'>
      { isbrands.map((brands) => {
        return <div className='col-lg-2 col-md-6' key={brands._id}>
          <div className='cursor-pointer product' id={brands._id} >
            <img src={brands.image} className='w-100' height={200} alt="" />
            <p className='font-lg fw-bold text-center'>{brands.name}</p>
          </div>
        </div>

      })}
      
    </div>
    </>
}
  </>
}
