import { useFormik } from 'formik'
import React, { useContext } from 'react'
import * as Yup from 'yup'
import { Helmet } from "react-helmet";
import { cartContext } from '../../Context/carts'
export default function Checkout() {

    let { Checkpayment, CartId } = useContext(cartContext)

    let validate = Yup.object({
        city: Yup.string().required('city is required'),
        details: Yup.string().required('details is required'),
        phone: Yup.string().required('phone is required').matches(/^01[0125][0-9]{0,8}$/, 'phone must be valid number...')
    })
    let formik = useFormik({
        initialValues: {
            city: '',
            details: '',
            phone: ''
        },
        // validationSchema: validate,
        onSubmit: handleSubmit,
    })
    async function handleSubmit(params) {
        let response = await Checkpayment(CartId, params)
        console.log(response);
        console.log(params);
        if (response?.data?.status === 'success') {
            console.log();
            window.location.href = response.data.session.url
        }
    }

    return <>
        <Helmet>
            <title>CheckOut</title>
        </Helmet>
        <div className='w-50 mx-auto py-4'>
            <h3>Checkout</h3>
            <form onSubmit={formik.handleSubmit}>
                <div className='my-2'>
                    <label htmlFor="Phone">Phone:</label>
                    <input value={formik.values.phone} onBlur={formik.handleBlur} onChange={formik.handleChange} type="cel" name="phone" id="phone" className='form-control mt-1' />
                    {formik.errors.phone && formik.touched.phone ? <div className='alert alert-danger mt-2'>{formik.errors.phone}</div> : ''}
                </div>
                <div className='my-2'>
                    <label htmlFor="details">details :</label>
                    <input value={formik.values.details} onBlur={formik.handleBlur} onChange={formik.handleChange} type="text" name="details" id="details" className='form-control mt-1' />
                    {formik.errors.details && formik.touched.details ? <div className='alert alert-danger mt-2'>{formik.errors.details}</div> : ''}
                </div>
                <div className='my-2'>
                    <label htmlFor="city">city :</label>
                    <input value={formik.values.city} onBlur={formik.handleBlur} onChange={formik.handleChange} type="text" name="city" id="city" className='form-control mt-1' />
                    {formik.errors.city && formik.touched.city ? <div className='alert alert-danger mt-2'>{formik.errors.city}</div> : ''}
                </div>
                <button type='submit' className='btn border-main bg-main text-white w-100 mt-3'>payment</button>
            </form>
        </div>
    </>
}
