import axios from 'axios'
import { Formik, useFormik } from 'formik'
import React, { useState } from 'react'
import { Navigate, useNavigate } from 'react-router-dom'
import *as Yup from 'yup'
import { Helmet } from "react-helmet";
export default function () {
    let Navigate = useNavigate()
    let BaseUrl = `https://route-ecommerce.onrender.com`
    const [MessErrors, setMessErrors] = useState()
    const [ResetCode, setResetCode] = useState(true)
    let validationSchema = Yup.object({
        email: Yup.string().required('email is required').email('Invalid email address')
    })

    let Formik1 = useFormik({
        initialValues: {
            email: ''
        },
        onSubmit: async (values) => {
            let { data }: any = await axios.post(`${BaseUrl}/api/v1/auth/forgotPasswords`, values).catch((error) => {
                setMessErrors(error.response.data.message);
            })
            console.log(data);
            if (data.statusMsg === "success") {
                setResetCode(false)
            }

        },
        validationSchema
    })
    let validat = Yup.object({
        resetCode: Yup.string().required('verifyResetCode is required').matches(/^[0-9]{1,6}$/, 'Invalid verifyResetCode number')
    })


    let Formik2 = useFormik({
        initialValues: {
            resetCode: ''
        },
        onSubmit: async (values) => {
            let { data }: any = await axios.post(`${BaseUrl}/api/v1/auth/verifyResetCode`, values).catch((errors) => { setMessErrors(errors.response.data.message) })
            console.log(data);
            if (data.status === "Success") {
                Navigate('/resetPassword')
                console.log(data);
            }

        },
        validationSchema: validat
    })


    return <>
 <Helmet>
      <title>Forgetpassword</title>
    </Helmet>
        {ResetCode ?
            <div className='my-2'>
                {MessErrors ? <div className='alert alert-danger'>{MessErrors}</div> : ''}
                <form onSubmit={Formik1.handleSubmit}>
                    <label htmlFor="email">Email:</label>
                    <input value={Formik1.values.email} onBlur={Formik1.handleBlur} onChange={Formik1.handleChange} type="email" name='email' id='email' className='form-control mt-2' />
                    {Formik1.errors.email ? <div className='alert alert-danger mt-2'>{Formik1.errors.email}</div> : ''}
                    <button type='submit' className='btn bg-main text-white mt-3'>Send Email</button>
                </form>
            </div> :
            <>
                <div className='my-2'>
                    {MessErrors ? <div className='alert alert-danger'>{MessErrors}</div> : ''}
                    <form onSubmit={Formik2.handleSubmit}>
                        <label htmlFor="resetCode">verifyResetCode:</label>
                        <input id='resetCode' type='text' name='resetCode' onChange={Formik2.handleChange} value={Formik2.values.resetCode} className='form-control mt-2' />
                        {Formik2.errors.resetCode ? <div className='alert alert-danger mt-2'>{Formik2.errors.resetCode}</div> : ''}
                        <button type='submit' className='btn bg-main text-white mt-3'>Verify Code</button>
                    </form>
                </div>
            </>
        }
    </>
}
