import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
import { Helmet } from "react-helmet";
export default function ResetPassword() {
  let BaseUrl = `https://route-ecommerce.onrender.com`

  let navigate = useNavigate()
  const [MessErrors, setMessErrors] = useState()
  const [BtnLoading, setBtnLoading] = useState(false)
  let validate = Yup.object({
    email: Yup.string().required('email is required').email('Invalid email address'),
    newPassword: Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/, 'password must start with uppercase ....'),
  })

  let formik = useFormik({
    initialValues: {
      email: '',
      newPassword: ''
    },
    validationSchema: validate,
    onSubmit: async (values) => {
      let { data }: any = await axios.put(`${BaseUrl}/api/v1/auth/resetPassword`, values).catch((errors) => { setMessErrors(errors.response.data.message)})
      console.log(data);
      if (data.token){
          navigate('/login')
      } }
  })

  return <>
    <Helmet>
      <title>ResetPassword</title>
    </Helmet>
  
  <div className='w-75 mx-auto py-4'>
      <h3>resetPassword</h3>
      {MessErrors ? <div className='alert alert-danger'>{MessErrors}</div> : ''}
      <form onSubmit={formik.handleSubmit}>
        <div className='my-2'>
          <label htmlFor="email">Email:</label>
          <input value={formik.values.email} onBlur={formik.handleBlur} onChange={formik.handleChange} type="email" name="email" id="email" className='form-control mt-1' />
          {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>{formik.errors.email}</div> : ''}
        </div>
        <div className='my-2'>
          <label htmlFor="newPassword">newPassword:</label>
          <input type="password" value={formik.values.newPassword} onBlur={formik.handleBlur} onChange={formik.handleChange} name="newPassword" id="newPassword" className='form-control mt-1' />
          {formik.errors.newPassword && formik.touched.newPassword ? <div className='alert alert-danger mt-2'>{formik.errors.newPassword}</div> : ''}
          <div className='pt-2 font-lg'>

         </div>
        </div>

        {BtnLoading ? <button type='button' className='btn bg-main text-white'><i className='fas fa-spinner fa-spin'></i></button>
          : <button type='submit' disabled={!(formik.isValid && formik.dirty)} className='btn bg-main text-white'>resetPassword</button>
        }
       
      </form>
    </div>
  </>
}
