import React, { useContext, useEffect, useState } from 'react'
import { cartContext } from '../../Context/carts'
import './cart.css'
import { Helmet } from "react-helmet";
import { toast } from 'react-hot-toast'
import { Link } from 'react-router-dom'
export default function Carts() {
  const [CartDetails, setCartDetails] = useState([])
  const [Loading, setLoading] = useState(false)
  let { getLoggedUserCart } = useContext(cartContext)
  let { RemoveData ,setnumOfCartItems } = useContext(cartContext)
  let { UpdateData } = useContext(cartContext)
  async function getCart() {
    setLoading(true)
    let respone = await getLoggedUserCart()
    console.log(respone.data.data.products);
    setCartDetails(respone.data.data)
    setLoading(false)
  }
  useEffect(() => {
    getCart()
  }, [])
  async function DeleteItems(productId) {
    let respone = await RemoveData(productId)
    console.log(respone);
    setnumOfCartItems(respone.data.numOfCartItems)
    toast.success('prodect Remove')
    setCartDetails(respone.data.data)
  }
  async function UpdateItems(productId, count) {
    let respone = await UpdateData(productId, count)
    toast.success('prodect Update count')
    setCartDetails(respone.data.data)
  }
  return <>
    <Helmet>
      <title>Carts</title>
    </Helmet>
    {Loading ? <div className='loading d-flex align-items-start justify-content-start bg-white position-fixed end-0  start-0 bottom-0 '>
      <div className="sk-cube-grid">
        <div className="sk-cube sk-cube1"></div>
        <div className="sk-cube sk-cube2"></div>
        <div className="sk-cube sk-cube3"></div>
        <div className="sk-cube sk-cube4"></div>
        <div className="sk-cube sk-cube5"></div>
        <div className="sk-cube sk-cube6"></div>
        <div className="sk-cube sk-cube7"></div>
        <div className="sk-cube sk-cube8"></div>
        <div className="sk-cube sk-cube9"></div>
      </div>

    </div> :
      <>
        {CartDetails !== null ? <>

          <div className="header py-3 fw-bold">
            <h1>Shopping Cart</h1>
          </div>
          <div className='row g-3 flex-wrap-reverse'>
            {CartDetails.products?.map((product) => {
              return <div className='col-lg-8' key={product.product._id}>
                <div className='card p-1'>
                  <div className='row' >
                    <div className='col-lg-3'>
                      <div className='images'>
                        <img src={product.product.imageCover} alt="" />
                      </div>
                    </div>
                    <div className="col-lg-9">
                      <div className='text text-black'>
                        <div className="head d-flex justify-content-between fw-bold pt-2 pb-2">
                          <span className="brand font-lg">{product.product.title}</span>
                          <span className="font-lg pe-2">{product.price} EGP </span>
                        </div>
                        <h1 className='h6 fw-bold text-main'>{product?.product?.brand?.name}</h1>
                      </div>
                      <h6 >Sold by <strong className='text-main'>FreshCart</strong></h6>
                      <div className='items'>
                        <button onClick={() => UpdateItems(product.product._id, product.count - 1)} className='btn border-main bg-main text-white btn-sm'>-</button>
                        <span className='mx-2'>{product.count}</span>
                        <button onClick={() => UpdateItems(product.product._id, product.count + 1)} className='btn border-main bg-main text-white btn-sm'>+</button>
                      </div>
                      <button onClick={() => { DeleteItems(product.product._id) }} className='btn border-main text-main mt-3'><i className='fa-regular text-main pe-2 fa-trash-can'></i>Remove</button>
                      <div className='d-flex align-items-end justify-content-end text-main fw-bolder'>
                        <span id="" className="a-size-medium sc-number-of-items"> Subtotal ({product.count} item):</span>
                        <span id="" className="a-color-price sc-price-container a-text-bold">&nbsp;
                          <span className="a-size-medium a-color-base sc-price sc-white-space-nowrap pe-2">{product.count * product.price} EGP</span></span>
                      </div>
                    </div>

                  </div>
                </div>
              </div>
            })}
            <div className='col-lg-4'>
              <div className='card'>
                <div className='d-flex justify-content-between fw-bolder'>
                  <span> Subtotal ({CartDetails.products?.length} items)</span>
                  <span>EGP {CartDetails.totalCartPrice}</span>
                </div>
                <div className='d-flex justify-content-between  fw-bolder'>
                  <span> Shipping Details</span>
                  <span className='text-main fw-bolder'>Free</span>
                </div>
                <div className='d-flex justify-content-between text-main  fw-bolder'>
                  <span className='font-lg'>Total :</span>
                  <span>EGP {CartDetails.totalCartPrice}</span>
                </div>
                <button className='btn bg-main  w-100 mt-4'><Link className='text-white' to={'/checkout'}>Proceed to Buy</Link></button>
              </div>
            </div>
          </div>
        </> : null
        }
      </>
    }

  </>
}
