import { useFormik } from 'formik'
import React, { useState } from 'react'
import intarface from './RegInterface'
import * as Yup from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { Helmet } from "react-helmet";
export default function Register() {
  let navigate = useNavigate();
  let [loading, Setloading] = useState(false)
  let [messgeEroor, SetmessgeEroor] = useState('')
  let BaseUrl = `https://route-ecommerce.onrender.com`
  let validate = Yup.object({
    name: Yup.string().required('name is required').min(3, 'name minlength is 3').max(16, 'name maxlength is 16'),
    email: Yup.string().required('email is required').email('Invalid email address'),
    password: Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/, 'password must start with uppercase ....'),
    rePassword: Yup.string().required('rePassword is required').oneOf([Yup.ref('password')], 'password and repassword doesn`t match....'),
    phone: Yup.string().required('phone is required').matches(/^01[0125][0-9]{0,8}$/, 'phone must be valid number...')
  })
  let formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      password: '',
      rePassword: '',
      phone: ''
    },
    validationSchema: validate,
    onSubmit: postdata,


  })
  async function postdata<T = any>(value: T) {
    Setloading(true)
    let { data }: any = await axios.post(`${BaseUrl}/api/v1/auth/signup`, value).catch((error) => {
      Setloading(false)
      console.log(error.response.data.message);
      SetmessgeEroor(error.response.data.message)
    })
    console.log(data);
    if (data.message === 'success') {
      Setloading(false)
      navigate('/login')
    }
  }
  return <>
   <Helmet>
      <title>Register</title>
    </Helmet>
    <div className='w-75 mx-auto py-4'>
      <h3>Register</h3>
      {messgeEroor ? <div className='alert alert-danger'>{messgeEroor}</div> : ''}
      <form onSubmit={formik.handleSubmit}>
        <div className='my-2'>
          <label htmlFor="name">Name:</label>
          <input value={formik.values.name} onBlur={formik.handleBlur} onChange={formik.handleChange} type="text" name="name" id="name" className='form-control mt-1' />
          {formik.errors.name && formik.touched.name ? <div className='alert alert-danger mt-2'>{formik.errors.name}</div> : ''}
        </div>
        <div className='my-2'>
          <label htmlFor="email">Email:</label>
          <input value={formik.values.email} onBlur={formik.handleBlur} onChange={formik.handleChange} type="email" name="email" id="email" className='form-control mt-1' />
          {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>{formik.errors.email}</div> : ''}
        </div>
        <div className='my-2'>
          <label htmlFor="password">Password:</label>
          <input value={formik.values.password} onChange={formik.handleChange} type="password" name="password" id="password" className='form-control mt-1' />
          {formik.errors.password && formik.touched.password ? <div className='alert alert-danger mt-2'>{formik.errors.password}</div> : ''}
        </div>
        <div className='my-2'>
          <label htmlFor="rePassword">rePassword:</label>
          <input value={formik.values.rePassword} onChange={formik.handleChange} type="password" name="rePassword" id="rePassword" className='form-control mt-1' />
          {formik.errors.rePassword && formik.touched.rePassword ? <div className='alert alert-danger mt-2'>{formik.errors.rePassword}</div> : ''}
        </div>
        <div className='my-2'>
          <label htmlFor="phone">Phone:</label>
          <input value={formik.values.phone} onChange={formik.handleChange} type="tel" name="phone" id="phone" className='form-control mt-1' />
          {formik.errors.phone && formik.touched.phone ? <div className='alert alert-danger mt-2'>{formik.errors.phone}</div> : ''}
        </div>
        {loading ? <button type='button' className='btn bg-main text-white'><i className='fas fa-spinner fa-spin'></i></button>
          : <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white'>Register</button>
        }
      </form>
    </div>

  </>
}
