import React from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Categories } from '../../interface/Categories';
import img1 from '../../assets/images/slider-image-1.jpeg'
import img2 from '../../assets/images/slider-image-2.jpeg'
import img3 from '../../assets/images/slider-image-3.jpeg'
export default function MainSilder() {
    return <>
        <div className='row g-0 pt-4'>
            <div className='col-lg-9 col-md-6'>
                <div className='images'>
                    <OwlCarousel className='owl-theme' autoPlay={true} autoplayTimeout={1000} items={1} loop>
                        <img src={img3} height={400} className='w-100' alt="" />
                        <img src={img1} height={400} className='w-100' alt="" />

                    </OwlCarousel>
                </div>
            </div>
            <div className='col-lg-3 col-md-6'>
                <div className='images'>
                    <img src={img2} className='w-100' height={200} alt="" />
                    <img src={img1} className='w-100' height={200} alt="" />
                </div>
            </div>
        </div>

    </>

}
